// visit from leaf to root
/*
DAG å¯¦ä¾‹ï¼š

DAG {
  names: [ 'benson' ],
  vertices: {
    benson: {
      name: 'benson',
      incoming: {},
      incomingNames: [],
      hasOutgoing: false,
      value: null
    }
  }
}
*/
class Dag {
    constructor() {
        this.names = [];
        this.vertices = {};
    }

    add(name) {
        // å–å¾—åƒç…§ï¼šç¯€é»æœ¬èº«ã€‚æˆ–æ’éŒ¯
        if (!name) {return;}
        if (this.vertices.hasOwnProperty(name)) {
            return this.vertices[name];
        }

        // å»ºæ§‹ï¼šç¯€é»
        let vertex = {
            name: name,
            incoming: {},
            incomingNames: [],
            hasOutgoing: false,
            value: null
        };
        this.vertices[name] = vertex;
        this.names.push(name);
        return vertex;
    }

    map(name, value) {
        this.add(name).value = value;
    }

    // visit all one vertex's incomming vertcies
    visit(vertex, fn, visited, path) {
        let name = vertex.name;
        let vertices = vertex.incoming;
        let names = vertex.incomingNames;
        let len = names.length

        if (!visited) {visited = {}}; // visited ç´€éŒ„å·²è¨ªç¯€é»ï¼Œè‹¥è‡ªå·±ç‚ºå·²è¨ªå•ç‹€æ…‹ï¼Œå°±çµæŸ Recursive fn 
        if (!path) {path = []}; // path ç”¨æ–¼ç™»è¨˜è¨ªå•æ¯å€‹ incoming çš„é †åº
        
        if (visited.hasOwnProperty(name)) {return}; // visited ç´€éŒ„å·²è¨ªç¯€é»ï¼Œè‹¥è‡ªå·±ç‚ºå·²è¨ªå•ç‹€æ…‹ï¼Œå°±çµæŸ Recursive fn 
        
        path.push(name);        // ç”¨ä¾†ç•¶ä½œ è¦éè¿´çš„fnå‡½æ•¸ çš„åƒæ•¸ ->> visit()ä¹‹å¾Œå¯å†é‡å° è·¯å¾‘åšå‹•ä½œï¼Œå‹•ä½œå®šç¾©å¤–åŒ…å‡ºå»
        
        visited[name] = true;   // ç‰©ä»¶å½¢å¼ï¼Œç™»è¨˜ç‚ºå·²è¨ªå•

        // Recursive fn        
        for (let i = 0; i < len; i++) {
            this.visit(vertices[names[i]], fn, visited, path);
        }
        fn(vertex, path); // éè¿´åŸ·è¡Œfn ä¾‹å¦‚ checkCycle() or topsortçš„callback
        path.pop(); 
    }

    addEdge(fromName, toName) {
        if (!fromName || !toName || fromName === toName) {return;}
        let from = this.add(fromName)
        let to = this.add(toName)
        if (to.incoming.hasOwnProperty(fromName)) {return;}
        
        function checkCycle(vertex, path) {
            if (vertex.name === toName) {
                throw new Error("cycle detected: " + toName + " <- " + path.join(" <- "));
            }
        }

        this.visit(from, checkCycle);
        from.hasOutgoing = true;
        to.incoming[fromName] = from;
        to.incomingNames.push(fromName);
    }

    addEdges(name, value, before, after) {
        this.map(name,value);
        if (before) {
            // if before is string
            if (Object.prototype.toString.call(before) === "[object String]") {
                this.addEdge(name, before)
            } 

            // if before is Array
            if (Array.isArray(before)) {
                before.forEach((vertex) => {
                    this.addEdge(name, vertex)
                })
            }
        }
        if (after) {
            // if after is string
            if (Object.prototype.toString.call(after) === "[object String]") {
                this.addEdge(after, name)
            } 

            // if after is Array
            if (Array.isArray(after)) {
                after.forEach((vertex) => {
                    this.addEdge(vertex, name)
                })
            }
        }
    }

    // topological sort  
    // visit path start from leaf
    topsort(fn) {
        let visited = {};
        let vertices = this.vertices;
        let names = this.names;
        let len = names.length;
        
        // è¿­ä»£æ¯å€‹ vertex ç¯€é»ï¼Œè‹¥ç‚ºæœ€å°¾ç«¯çš„ï¼Œå‰‡ä½¿éè¿´(æ‰£æ‰ä»–å¾Œå°å‰©é¤˜çš„å†éè¿´æ‰¾)    
        for (let i = 0; i < len; i ++) {
            let vertex = vertices[names[i]];
            if (!vertex.hasOutgoing) {
                this.visit(vertex, fn, visited)
            };

        };
    };
}

module.exports = Dag
